import { useContext } from "react";
import { Popup, displayContactContext } from "../../index";
import "./contact.scss";

const Contact = () => {
    const display = useContext(displayContactContext);

    const content = (
        <div className="contact">
            <h1> Contact Us </h1>
            <h3>contact@livesportly.com</h3>
            <span> our response could be slow. we will replay back as soon as possible</span>
        </div>
    );
    return (
        <div id="CONTACT">
            <Popup content={content} setDisplay={display} />
        </div>
    );
};

export default Contact;
